@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: unset;
}

ul {
  list-style: none;
}

body {
  font-family: "Open Sans", sans-serif;
}

.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  margin-bottom: 1rem;
  padding: 0 1rem;
}

.profile h2 {
  font-family: "norwesterregular", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #165063;
  font-weight: unset;
}

.profile__image {
  max-width: 5rem;
  margin: .5rem 0;
}

.profile__image img {
  border: 1px solid;
  border-color: #222B34;
  border-radius: 50%;
  max-width: 100%;
  height: auto;
  mix-blend-mode: luminosity;
}

.profile__post-handler {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
  width: 100%;
}

.profile__post-handler button {
  width: 8rem;
  padding: .5rem 1rem;
  text-transform: capitalize;
  border: none;
  border-radius: 1rem;
  background: #222B34;
  color: #F7F7F7;
}

.profile__post-handler button:nth-of-type(1) {
  background: #165063;
}

.profile__post-handler button:nth-of-type(2) {
  transition: background .3s;
}

.profile__post-handler button:nth-of-type(2).active {
  background: #165063;
}

@media (hover: hover) {
  .profile__post-handler button:hover {
    background: #1C6881;
  }
}

.profile__logo {
  margin-top: 3rem;
  width: 7.1875rem;
}

.profile__logo svg {
  max-width: 100%;
}

@media screen and (min-width: 768px) {
  .profile__image {
    max-width: 7.8125rem;
  }
  .profile__post-handler button {
    width: 10rem;
  }
  .profile__logo {
    width: 8.75rem;
  }
}

@media screen and (min-width: 1200px) {
  .profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 60%;
    margin: 0 auto;
  }
  .profile h2 {
    margin-top: 1.5rem;
  }
  .profile__image {
    order: 4;
    max-width: 10rem;
    margin-top: 2rem;
  }
  .profile__post-handler {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    align-content: center;
    margin-top: 3.3rem;
  }
  .profile__post-handler button {
    width: 12rem;
    border-radius: 2rem;
  }
  .profile__post-handler button:nth-of-type(1) {
    background: #165063;
    margin-bottom: 1rem;
  }
  .profile__logo {
    width: 11.5625rem;
  }
}

.post-method {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
  width: 100%;
  background: #165063;
  padding: .8rem 1rem;
}

.post-method button {
  text-transform: capitalize;
  letter-spacing: 1px;
  padding: .4rem 1rem;
  border-radius: 2rem;
  outline: none;
  border: none;
  background: #1C6881;
  color: #F7F7F7;
  font-weight: 600;
  margin-right: 1rem;
  transition: all .3s;
}

.post-method button:disabled {
  background: white;
  color: black !important;
}

@media (hover: hover) {
  .post-method button:hover {
    color: #222B34;
  }
}

@media screen and (min-width: 1200px) {
  .post-method {
    border-radius: 0 1rem 0 0;
    right: -1px;
    top: -1px;
  }
}

@media screen and (min-width: 1900px) {
  .post-method {
    padding-left: 5rem;
    padding-right: 5rem;
  }
}

.post-holder {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  padding: .5rem;
  color: #F7F7F7;
  background: #222B34;
  border-radius: .5rem;
  width: 100%;
  margin-bottom: 1.5rem;
  letter-spacing: .5px;
}

.post-holder label {
  margin-right: .5rem;
}

.post-holder .form-input {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  width: 100%;
  margin: .3rem .5rem;
  letter-spacing: 1px;
}

.post-holder .form-input input,
.post-holder .form-input textarea {
  max-width: 100%;
  height: 100%;
  background: #222B34;
  color: #F7F7F7;
  padding: .4rem;
  border: 1px solid;
  border-color: #165063;
  letter-spacing: 1px;
  transition: background .3s;
}

.post-holder .form-input input:disabled,
.post-holder .form-input textarea:disabled {
  border: none;
  outline: none;
  background: transparent;
  color: #F7F7F7;
  scrollbar-color: gray;
}

.post-holder .form-input:nth-of-type(4) {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
}

.post-holder .post-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  margin: 1rem auto;
}

.post-holder .post-button button {
  letter-spacing: 1px;
  padding: .4rem 1rem;
  border-radius: 2rem;
  outline: none;
  border: none;
  background: #1C6881;
  color: #F7F7F7;
  font-weight: 600;
}

.post-holder .post-button button:nth-of-type(1) {
  margin-right: 1rem;
}

.post-holder .post-button button:disabled {
  background: white;
  color: black !important;
}

@media (hover: hover) {
  .post-holder .post-button button:hover {
    color: #222B34;
  }
}

.post-holder:nth-of-type(1) {
  margin-top: 2rem;
}

.post-holder.active {
  background: #165063;
}

.admin-login {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  width: 100vw;
  height: 100vh;
  background: #F7F7F7;
}

.admin-login img {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  mix-blend-mode: luminosity;
  width: 15.625rem;
}

.admin-login form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  border-radius: 2rem;
  box-shadow: 0 0 6px #00000020;
  background: #ffffffcf;
  width: 70vmin;
  height: 80vmin;
}

.admin-login form div {
  position: relative;
  display: flex;
}

.admin-login form div label {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  pointer-events: none;
  font-weight: bold;
  opacity: .4;
  transition: 150ms all linear;
}

.admin-login form div:nth-of-type(1) {
  margin-bottom: 1rem;
}

.admin-login form div input {
  padding: 0 1rem;
  padding-bottom: .5rem;
  padding-top: 1.2rem;
  outline: none;
  border-radius: 2rem;
  background: transparent;
  border: 2px solid gray;
  text-align: center;
  width: 45vmin;
}

.admin-login form div input:focus, .admin-login form div input:valid {
  border-color: #165063;
  color: #1C6881;
}

.admin-login form div input:focus ~ label, .admin-login form div input:valid ~ label {
  width: 30%;
  height: 30%;
  margin-bottom: 1rem;
  left: 1.1rem;
  top: .5rem;
  color: #165063;
  opacity: 1;
}

.admin-login form button {
  margin-top: 1rem;
  margin-bottom: 2rem;
  padding: .5rem 1rem;
  border-radius: 2rem;
  border: none;
  background: #165063;
  color: #F7F7F7;
  outline: none;
}

@media (hover: hover) {
  .admin-login form button:hover {
    background: #1C6881;
  }
}

.admin-login form span {
  position: relative;
  text-decoration: underline;
  font-style: italic;
}

@media (hover: hover) {
  .admin-login form span:hover {
    color: #1C6881;
  }
}

@media screen and (min-width: 768px) {
  .admin-login img {
    width: 20.625rem;
  }
  .admin-login form div input {
    padding-top: 1.5rem;
    border: 4px solid gray;
  }
}

@media screen and (min-width: 1900px) {
  .admin-login img {
    width: 28.75rem;
  }
  .admin-login form div input {
    padding-top: 2rem;
  }
}

.modal-new-post {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: fixed;
  background: #0e0e0e78;
  z-index: 99;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: 150ms all linear;
  transform-origin: center;
}

.modal-new-post.active {
  opacity: 1;
  visibility: visible;
}

.modal-new-post .form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  width: 60vmin;
  height: auto;
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  margin-top: 1rem;
}

.modal-new-post .form-container__title {
  text-align: center;
  font-weight: 600;
  margin-bottom: 1rem;
}

.modal-new-post .form-container form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  height: 100%;
}

.modal-new-post .form-container form input {
  outline: none;
}

.modal-new-post .form-container form input::-webkit-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form input:-ms-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form input::-ms-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form input::placeholder {
  opacity: .3;
}

.modal-new-post .form-container form input:nth-of-type(1) {
  width: 100%;
  padding: .5rem 1rem;
  border-radius: .5rem;
  font-weight: 600;
  border-style: solid;
  text-align: center;
}

.modal-new-post .form-container form input:nth-of-type(2) {
  margin-top: 1rem;
  border-radius: 15rem;
  padding: .2rem .5rem;
  font-weight: 600;
  font-style: italic;
  background: white;
  transition: 200ms background linear;
}

.modal-new-post .form-container form textarea {
  outline: none;
  width: 100%;
  padding: .5rem 1rem;
  margin-top: 1rem;
  border-radius: .5rem;
  padding-bottom: 2.5rem;
  font-style: italic;
  letter-spacing: 1px;
  font-weight: 600;
  border-width: 2px;
  border-style: solid;
}

.modal-new-post .form-container form textarea::-webkit-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form textarea:-ms-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form textarea::-ms-input-placeholder {
  opacity: .3;
}

.modal-new-post .form-container form textarea::placeholder {
  opacity: .3;
}

.modal-notification {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: fixed;
  background: #0e0e0e78;
  z-index: 99;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: 150ms all linear;
  transform-origin: center;
}

.modal-notification.active {
  opacity: 1;
  visibility: visible;
}

.modal-notification-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  width: 60vmin;
  height: auto;
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  margin-top: 1rem;
}

.modal-notification-container__title {
  text-align: center;
