}

.login-page {
  width: 100vw;
  height: 100vh;
  background: #F7F7F7;
}

.login-page-cont {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

.login-page-cont img {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  mix-blend-mode: luminosity;
  width: 15.625rem;
}

.login-page-cont__info {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
  z-index: 1;
  margin: auto;
  max-width: 15rem;
  padding: 3rem 2rem;
  border-radius: 2rem;
  box-shadow: 0 0 6px #00000020;
  background: #ffffffcf;
  width: 100%;
}

.login-page-cont__info button {
  padding: .8rem .5rem;
  outline: none;
  border: none;
  border-radius: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: 100ms all linear;
}

.login-page-cont__info button:nth-of-type(1) {
  background: #165063;
  color: #F7F7F7;
}

.login-page-cont__info button:nth-of-type(2) {
  background: #222B34;
  color: #F7F7F7;
}

@media (hover: hover) {
  .login-page-cont__info button:hover {
    transform: scale(1.1);
  }
}

@media screen and (min-width: 768px) {
  .login-page img {
    width: 20.625rem;
  }
  .login-page-cont__info {
    max-width: 20rem;
  }
}

@media screen and (min-width: 1200px) {
  .login-page-cont img {
    width: 20.625rem;
  }
  .login-page-cont__info {
    max-width: 25rem;
  }
  .login-page-cont__info button {
    padding: .8rem 1rem;
  }
}

@media screen and (min-width: 1900px) {
  .login-page-cont img {
    width: 28.75rem;
  }
  .login-page-cont__info {
    max-width: 30rem;
    padding: 4rem 2rem;
  }
  .login-page-cont__info button {
    letter-spacing: 1px;
  }
}

.dashboard {
  position: relative;
  width: 100%;
  background: #222B34;
  min-height: 100vh;
  padding: 1.5rem;
}

.dashboard button {
  outline: none;
}

.dashboard button:disabled {
  opacity: .5;
  color: gray;
}

.dashboard-bg {
  position: fixed;
  z-index: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  mix-blend-mode: luminosity;
}

.dashboard-top {
  text-align: right;
  color: #F7F7F7;
  max-width: 20rem;
  margin: 0 auto;
  cursor: pointer;
  font-weight: 600;
}

@media (hover: hover) {
  .dashboard-top span:hover {
    color: #1C6881;
  }
}

.dashboard-cont {
  position: relative;
  background: #ffffffcf;
  border-radius: 1rem;
  max-width: 20rem;
  margin: 1rem auto;
}

.dashboard-cont-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  padding: 2rem 0rem;
}

.dashboard-cont-content__info {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
}

.dashboard-cont-content__info__posts {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  overflow-y: scroll;
  padding: 1rem;
  padding-top: 0;
  width: 100%;
  height: 25rem;
  margin-top: 1rem;
}

@media screen and (min-width: 768px) {
  .dashboard-top {
    max-width: 40rem;
  }
  .dashboard-cont {
    max-width: 40rem;
  }
}

@media screen and (min-width: 1200px) {
  .dashboard-top {
    max-width: 60rem;
    margin-bottom: 1rem;
  }
  .dashboard-cont {
    max-width: 60rem;
    margin: auto;
    height: 50rem;
  }
  .dashboard-cont-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-start;
    padding: 0;
  }
  .dashboard-cont-content__info__posts {
    height: 40rem;
  }
}

@media screen and (min-width: 1900px) {
  .dashboard-top {
    max-width: 80rem;
  }
  .dashboard-cont {
    max-width: 80rem;
  }
  .dashboard-cont-content__info__posts {
    max-width: 41rem;
  }
}

